import React from 'react'

import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit'

import classes from '../Merchant/Merchant.module.css'

const Merchant = (props) => {
   return(
      <div className={classes.merchantCard}>
         <div className={classes.container1}>
            <p className={classes.merchantName}>{props.name}</p>
         </div>
         <div className={classes.container2}>
            <p>
               <span>
                  <Button onClick={props.editMerchant} variant="contained" color="primary" className={classes.edit}>
                     Edit
                  <EditIcon className={classes.rightIcon} />
                  </Button>
               </span>
               <span>
                  <Button onClick={props.deleteMerchant} variant="contained" color="secondary" className={classes.button}>
                     Delete
                  <DeleteIcon className={classes.rightIcon} />
                  </Button>
               </span>                         
            </p>
            
         </div>


      </div>
   )
}

export default Merchant;