import React from 'react';
import classes from'./App.css';



import MerchantContainer from './components/MerchantContainer/MerchantContainer'
// import AddMerchant from './components/MerchantContainer/AddMerchant/AddMerchant'
import Login from './components/Login/login'
import { BrowserRouter as Router, Switch ,Route } from 'react-router-dom'
import {ProtectedRoute} from './ProtectedRoute'


function App() {
	return (
		<Router>
			<div className={classes.App}>

				{/* <Nav/> */}
				<Switch>
					<Route 
						path="/" 
						exact 
						component={Login} 
					/>
					//Protected route for merchant Dashboard
					<ProtectedRoute 
						path="/merchant-list" 
						exact 
						component={MerchantContainer} 
					/>
					<Route path="*" component = {() => "ERROR 404 PAGE NOT FOUND"}/>
				</Switch>
				
			</div>
		</Router>
	);
}

export default App;
