import React from "react"

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import CheckIcon from '@material-ui/icons/Check';
import classes from '../AddMerchant/AddMerchant.module.css'


class AddMerchant extends React.Component {

   state = {
      merchantInfo : {
         name: '',
         shippingFees: [
            {
               MetroManila: {
                  Box: 0,
                  Oversized: 0,
                  BigPouch: 0,
                  SmallPouch: 0
               }
            },
            {
               Provincial: {
                  Box: 0,
                  Oversized: 0,
                  BigPouch: 0,
                  SmallPouch: 0
               }
            },
            {
               IntraProvincial: {
                  Box: 0,
                  Oversized: 0,
                  BigPouch: 0,
                  SmallPouch: 0
               }
            }
         ]
      }
   }

   componentDidMount(){
      
   }


   handleChange = e => {
      e.preventDefault()

      //Get Merchant Info from state
      const merchant = this.state.merchantInfo

      const { name, value } = e.target
      


      //Switch for updating state from forms
      switch (name) {
         case "name":
            merchant.name = e.target.value
            break;

         case "metroManilaBox":
            merchant.shippingFees[0].MetroManila.Box = Number(e.target.value)
            break;

         case "metroManilaOversized":
            merchant.shippingFees[0].MetroManila.Oversized = Number(e.target.value)
            break;

         case "metroManilaBigPouch":
            merchant.shippingFees[0].MetroManila.BigPouch = Number(e.target.value)
            break;

         case "metroManilaSmallPouch":
            merchant.shippingFees[0].MetroManila.SmallPouch = Number(e.target.value)
            break;

         case "provincialBox":
            merchant.shippingFees[1].Provincial.Box = Number(e.target.value)
            break;

         case "provincialOversized":
            merchant.shippingFees[1].Provincial.Oversized = Number(e.target.value)
            break;

         case "provincialBigPouch":
            merchant.shippingFees[1].Provincial.BigPouch = Number(e.target.value)
            break;

         case "provincialSmallPouch":
            merchant.shippingFees[1].Provincial.SmallPouch = Number(e.target.value)
            break;

         case "intraBox":
            merchant.shippingFees[2].IntraProvincial.Box = Number(e.target.value)
            break;

         case "intraOversized":
            merchant.shippingFees[2].IntraProvincial.Oversized = Number(e.target.value)
            break;

         case "intraBigPouch":
            merchant.shippingFees[2].IntraProvincial.BigPouch = Number(e.target.value)
            break;

         case "intraSmallPouch":
            merchant.shippingFees[2].IntraProvincial.SmallPouch = Number(e.target.value)
            break;

         default:
            break;
      }

      this.setState({
         merchantInfo: merchant
      })

   }

   render() {
      let merchantInfo = this.state.merchantInfo
      return (
         <div className={classes.container}>
            <p id={classes.header}>Add Merchant Info</p>
            <form>
               <TextField
                  name= 'name'
                  onChange = {this.handleChange}
                  id="standard-full-width"
                  value = {this.state.merchantInfo.name}
                  label = "Merchant Name"
                  style={{
                     width: "60%"
                  }}
                  placeholder="Merchant Name"
                  margin="normal"
               />

               <h4>Shipping Fee</h4>

               <div className="Accordion">

                  <div className="AccordionItem">
                     <p>Metro Manila</p>
                     <div className="accordionForm">
                        <span>
                           <TextField
                              name='metroManilaBox'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Box"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>
                        <span>
                           <TextField
                              name='metroManilaOversized'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Oversized"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>
                        <span>
                           <TextField
                              name='metroManilaBigPouch'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Big Pouch"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>
                        <span>
                           <TextField
                              name='metroManilaSmallPouch'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Small Pouch"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>

                     </div>
                  </div>

                  <div className="AccordionItem">
                     <p>Provincial</p>
                     <span>
                        <TextField
                           name='provincialBox'
                           onChange={this.handleChange}
                           id="outlined-number"
                           label="Box"
                           // value={values.age}
                           // onChange={handleChange('age')}
                           type="number"
                           className={classes.textField}
                           InputLabelProps={{
                              shrink: true,
                           }}
                           margin="normal"
                           variant="outlined"
                        />
                     </span>
                     <span>
                        <TextField
                           name='provincialOversized'
                           onChange={this.handleChange}
                           id="outlined-number"
                           label="Oversized"
                           // value={values.age}
                           // onChange={handleChange('age')}
                           type="number"
                           className={classes.textField}
                           InputLabelProps={{
                              shrink: true,
                           }}
                           margin="normal"
                           variant="outlined"
                        />
                     </span>
                     <span>
                        <TextField
                           name='provincialBigPouch'
                           onChange={this.handleChange}
                           id="outlined-number"
                           label="Big Pouch"
                           // value={values.age}
                           // onChange={handleChange('age')}
                           type="number"
                           className={classes.textField}
                           InputLabelProps={{
                              shrink: true,
                           }}
                           margin="normal"
                           variant="outlined"
                        />
                     </span>
                     <span>
                        <TextField
                           name='provincialSmallPouch'
                           onChange={this.handleChange}
                           id="outlined-number"
                           label="Small Pouch"
                           // value={values.age}
                           // onChange={handleChange('age')}
                           type="number"
                           className={classes.textField}
                           InputLabelProps={{
                              shrink: true,
                           }}
                           margin="normal"
                           variant="outlined"
                        />
                     </span>
                  </div>

                  <div className="AccordionItem">
                     <p>Intra Provincial</p>
                     <div className="accordionForm">
                        <span>
                           <TextField
                              name='intraBox'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Box"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>
                        <span>
                           <TextField
                              name='intraOversized'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Oversized"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>
                        <span>
                           <TextField
                              name='intraBigPouch'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Big Pouch"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>
                        <span>
                           <TextField
                              name='intraSmallPouch'
                              onChange={this.handleChange}
                              id="outlined-number"
                              label="Small Pouch"
                              // value={values.age}
                              // onChange={handleChange('age')}
                              type="number"
                              className={classes.textField}
                              InputLabelProps={{
                                 shrink: true,
                              }}
                              margin="normal"
                              variant="outlined"
                           />
                        </span>
                     </div>
                  </div>

               </div>

               <div className={classes.formButtons}>
                  <span>
                     <Button 
                        variant="contained" 
                        onClick={()=>this.props.enrollMerchant(merchantInfo)} 
                        color="default" 
                        className={classes.button}>
                        Add Merchant
								<CheckIcon className={classes.rightIcon} />
                     </Button>
                  </span>
                  <span>
                     <Button 
                        variant="contained" 
                        color="secondary" 
                        onClick={this.props.cancelEdit}
                        className={classes.button}>
                        Cancel
								<CloseIcon className={classes.rightIcon} />
                     </Button>
                  </span>


               </div>

            </form>

         </div>
      )
   }
}

export default AddMerchant;