import React from "react"
import Swal from 'sweetalert2'
// import { Link } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import CheckIcon from '@material-ui/icons/Check';

import classes from '../MerchantEdit/MerchantEdit.module.css'
import { async } from "q";

class MerchantEdit extends React.Component {
   
	state = { 
		updateMerchantInfo : {
			id: this.props.merchantInfo.id,
			name: this.props.merchantInfo.name,
			shippingFees: this.props.merchantInfo.shippingFees
		},
	}

	baseState = this.state
	

	componentDidMount = () => {
		
	}


	//Handle Form Changes
	handleChange = e => {
		e.preventDefault()

		//Get Merchant Info from state
		const merchant = this.state.updateMerchantInfo

		const { name ,value } = e.target

		//Switch for updating state from forms
		switch (name) {
			case "name" : 
				merchant.name = e.target.value
			break;

			case "metroManilaBox" :
				merchant.shippingFees[0].MetroManila.Box = Number(e.target.value)
			break;

			case "metroManilaOversized":
				merchant.shippingFees[0].MetroManila.Oversized = Number(e.target.value)
			break;

			case "metroManilaBigPouch":
				merchant.shippingFees[0].MetroManila.BigPouch = Number(e.target.value)
			break;

			case "metroManilaSmallPouch":
				merchant.shippingFees[0].MetroManila.SmallPouch = Number(e.target.value)
			break;

			case "provincialBox":
				merchant.shippingFees[1].Provincial.Box = Number(e.target.value)
			break;

			case "provincialOversized":
				merchant.shippingFees[1].Provincial.Oversized = Number(e.target.value)
			break;

			case "provincialBigPouch":
				merchant.shippingFees[1].Provincial.BigPouch = Number(e.target.value)
			break;

			case "provincialSmallPouch":
				merchant.shippingFees[1].Provincial.SmallPouch = Number(e.target.value)
			break;

			case "intraBox":
				merchant.shippingFees[2].IntraProvincial.Box = Number(e.target.value)
				break;

			case "intraOversized":
				merchant.shippingFees[2].IntraProvincial.Oversized = Number(e.target.value)
				break;

			case "intraBigPouch":
				merchant.shippingFees[2].IntraProvincial.BigPouch = Number(e.target.value)
				break;

			case "intraSmallPouch":
				merchant.shippingFees[2].IntraProvincial.SmallPouch = Number(e.target.value)
				break;



			default: 
			break;
		}

		this.setState({
			updateMerchantInfo : merchant
		})

	}

	handleCancel = async (id) => {
		Swal.fire({
			title: 'Are you sure?',
			text: "Your changes will not be saved",
			type: 'warning',
			showCancelButton: true,
			confirmButtonColor: '#3085d6',
			cancelButtonColor: '#d33',
			confirmButtonText: 'Proceed'
		}).then( async (result) => {
			if (result.value) {
				await this.setState({
					updateMerchantInfo : this.baseState
				})
				this.props.cancelEdit(id)
			}
		})
	}


   render() {

		const merchantInfo = this.state.updateMerchantInfo

      return (
         <div className={classes.container}>
            <p id={classes.header}>Edit Merchant Info</p>
            <form>
					<TextField
						id="standard-full-width"
						label="Merchant Name"
						style={{ 
							width: "60%"
						}}
						name='name'
						onChange={this.handleChange}
						value={merchantInfo.name}
						placeholder="Merchant Name"
						margin="normal"
					/>

               <h4>Shipping Fee</h4>

               <div className ="Accordion">
                  
                  <div className ="AccordionItem">
                     <p>Metro Manila</p>
                     <div className="accordionForm">
								<span>
									<TextField
										id="outlined-number"
										label="Box"
										name="metroManilaBox"
										onChange={this.handleChange}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										value={ merchantInfo.shippingFees ? merchantInfo.shippingFees[0].MetroManila.Box : 0}
										margin="normal"
										variant="outlined"
									/>
								</span>
								<span>
									<TextField
										id="outlined-number"
										label="Oversized"
										name = 'metroManilaOversized'
										value={merchantInfo.shippingFees ? merchantInfo.shippingFees[0].MetroManila.Oversized : 0}
										onChange={this.handleChange}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										margin="normal"
										variant="outlined"
									/>
								</span>
								<span>
									<TextField
										id="outlined-number"
										label="Big Pouch"
										name="metroManilaBigPouch"
										// value={values.age}
										onChange={this.handleChange}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										value={merchantInfo.shippingFees ? merchantInfo.shippingFees[0].MetroManila.BigPouch : 0}
										margin="normal"
										variant="outlined"
									/>
								</span>
								<span>
									<TextField
										id="outlined-number"
										label="Small Pouch"
										name="metroManilaSmallPouch"
										// value={values.age}
										onChange={this.handleChange}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										value={merchantInfo.shippingFees ? merchantInfo.shippingFees[0].MetroManila.SmallPouch : 0}
										margin="normal"
										variant="outlined"
									/>
								</span>
                        
                     </div>
                  </div>
                  
                  <div className ="AccordionItem">
                     <p>Provincial</p>
							<span>
								<TextField
									id="outlined-number"
									label="Box"
									name="provincialBox"
									value={merchantInfo.shippingFees ? merchantInfo.shippingFees[1].Provincial.Box : 0}
									
									onChange={this.handleChange}
									type="number"
									className={classes.textField}
									InputLabelProps={{
										shrink: true,
									}}
									margin="normal"
									variant="outlined"
								/>
							</span>
							<span>
								<TextField
									id="outlined-number"
									label="Oversized"
									name = "provincialOversized"
									value={merchantInfo.shippingFees ? merchantInfo.shippingFees[1].Provincial.Oversized : 0}
									
									onChange={this.handleChange}
									type="number"
									className={classes.textField}
									InputLabelProps={{
										shrink: true,
									}}
									margin="normal"
									variant="outlined"
								/>
							</span>
							<span>
								<TextField
									id="outlined-number"
									label="Big Pouch"
									name="provincialBigPouch"
									onChange={this.handleChange}
									type="number"
									className={classes.textField}
									InputLabelProps={{
										shrink: true,
									}}
									value={merchantInfo.shippingFees ? merchantInfo.shippingFees[1].Provincial.BigPouch : 0}
									margin="normal"
									variant="outlined"
								/>
							</span>
							<span>
								<TextField
									id="outlined-number"
									label="Small Pouch"
									name = "provincialSmallPouch"
									// value={values.age}
									onChange={this.handleChange}
									type="number"
									className={classes.textField}
									InputLabelProps={{
										shrink: true,
									}}
									value={merchantInfo.shippingFees ? merchantInfo.shippingFees[1].Provincial.SmallPouch : 0}
									margin="normal"
									variant="outlined"
								/>
							</span>
                  </div>
                  
                  <div className ="AccordionItem">
                     <p>Intra Provincial</p>
                     <div className="accordionForm">
								<span>
									<TextField
										id="outlined-number"
										label="Box"
										name="intraBox"
										onChange={this.handleChange}
										// onChange={handleChange('age')}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										value={merchantInfo.shippingFees ? merchantInfo.shippingFees[2].IntraProvincial.Box : 0}
										margin="normal"
										variant="outlined"
									/>
								</span>
								<span>
									<TextField
										id="outlined-number"
										label="Oversized"
										name="intraOversized"
										// value={values.age}
										onChange={this.handleChange}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										value={merchantInfo.shippingFees ? merchantInfo.shippingFees[2].IntraProvincial.Oversized : 0}
										margin="normal"
										variant="outlined"
									/>
								</span>
								<span>
									<TextField
										id="outlined-number"
										label="Big Pouch"
										name="intraBigPouch"
										// value={values.age}
										onChange={this.handleChange}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										value={merchantInfo.shippingFees ? merchantInfo.shippingFees[2].IntraProvincial.BigPouch : 0}
										margin="normal"
										variant="outlined"
									/>
								</span>
								<span>
									<TextField
										id="outlined-number"
										label="Small Pouch"
										name ="intraSmallPouch"
										// value={values.age}
										onChange={this.handleChange}
										type="number"
										className={classes.textField}
										InputLabelProps={{
											shrink: true,
										}}
										value={merchantInfo.shippingFees ? merchantInfo.shippingFees[2].IntraProvincial.SmallPouch : 0}
										margin="normal"
										variant="outlined"
									/>
								</span>
                     </div>
                  </div>

               </div>

               <div className={classes.formButtons}> 
						<span>
							<Button
								onClick={() => this.props.updateMerchant(merchantInfo.id,merchantInfo)}
								variant="contained" 
								color="default" 
								className={classes.button}>
								Update
								<CheckIcon className={classes.rightIcon} />
							</Button>			
						</span>
						<span>
							<Button 
								onClick={this.handleCancel} 
								variant="contained" color="secondary" 
								className={classes.button}>
								Cancel
								<CloseIcon className={classes.rightIcon} />
							</Button>		
						</span>
						
						
               </div>

            </form>

         </div>
      )
   }
}

export default MerchantEdit;