

class Auth {
   constructor() {
      this.authenticated = false
   }

   login(cb) {
      localStorage.setItem('isAuthenticated',true)
      cb()
   }

   logout(cb){
      localStorage.setItem('isAuthenticated',false)
      
      cb()
   }

   isAuthenticated(){
      return this.isAuthenticated
   }

}

export default new Auth()