import React from 'react';

import TextField from '@material-ui/core/TextField';
import Swal from 'sweetalert2'

import MerchantEdit from './MerchantEdit/MerchantEdit'
import MerchantList from './MerchantList/MerchantList'
import AddMerchant from './AddMerchant/AddMerchant'
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import ListIcon from '@material-ui/icons/Assignment';
import SideNav from '../partials/SideNav/SideNav'
import BurgerMenu from '../partials/BurgerMenu/burgerMenu'
import auth from '../../auth'
import classes from './MerchantContainer.module.css';

class MerchantContainer extends React.Component {

   state = {
      //Dummy merchant data
      merchants : [

         {  
            id: 1,
            name : 'Nike',
            shippingFees: [
               {
                  'MetroManila': 
                  {
                     'Box' : 100,
                     'Oversized' : 150,
                     'BigPouch' : 100,
                     'SmallPouch' : 50
                  }
               },
               {
                  'Provincial':
                  {
                     'Box': 120,
                     'Oversized': 180,
                     'BigPouch': 110,
                     'SmallPouch': 80
                  }
               },
               {
                  'IntraProvincial':
                  {
                     'Box': 130,
                     'Oversized': 190,
                     'BigPouch': 130,
                     'SmallPouch': 100
                  }
               }
            ]
         }, 
         {  
            id:2,
            name: 'Addidas',
            shippingFees: [
               {
                  'MetroManila': {
                     'Box': 100,
                     'Oversized': 150,
                     'BigPouch': 100,
                     'SmallPouch': 50
                  }
               },
               {
                  'Provincial': {
                     'Box': 120,
                     'Oversized': 180,
                     'BigPouch': 110,
                     'SmallPouch': 80
                  }
               },
               {
                  'IntraProvincial': {
                     'Box': 130,
                     'Oversized': 190,
                     'BigPouch': 130,
                     'SmallPouch': 100
                  }
               }
            ]
         },
         {
            id: 3,
            name: 'Converse',
            shippingFees: [
               {
                  'MetroManila': {
                     'Box': 100,
                     'Oversized': 150,
                     'BigPouch': 100,
                     'SmallPouch': 50
                  }
               },
               {
                  'Provincial': {
                     'Box': 120,
                     'Oversized': 180,
                     'BigPouch': 110,
                     'SmallPouch': 80
                  }
               },
               {
                  'IntraProvincial': {
                     'Box': 130,
                     'Oversized': 190,
                     'BigPouch': 130,
                     'SmallPouch': 100
                  }
               }
            ]
         }
         
      ],
      merchantInfo:{},
      merchantSearch : "",

      //Checkers for components
      editMerchant: false,
      addMerchant: false,

   }

   //Set add merchant to true
   addMerchant = () =>{
      this.setState({
         ...this.state,
         addMerchant: true,
         editMerchant: false
      })
   }

   //Add Merchant Info
   enrollMerchant = (merchantInfo) => {
      
      const merchants = this.state.merchants
      let newMerchant = {}
      newMerchant.id = merchants.length + 1
      newMerchant.name = merchantInfo.name
      newMerchant.shippingFees = merchantInfo.shippingFees

      merchants.push(newMerchant)

      
      
      Swal.fire(
         'Success!',
         'You have added a new merchant.',
         'success'
         )
      this.setState({
         merchants: merchants,
         addMerchant : false
      })
   }

   editMerchant = (id) => {

      this.setState({
         ...this.state,
         addMerchant: false,
         editMerchant: true,
      })
   
      const merchants = this.state.merchants

      for (const key in merchants) {
         if (merchants.hasOwnProperty(key)) {
            if (id === merchants[key].id) {
               this.setState({
                  merchantInfo: merchants[key]
               })
            }
         }
      }

   }

   cancelEdit = () => {    
      this.setState({
         ...this.state,
         editMerchant: false,
         addMerchant: false
         
      })

      window.location.reload()
   }

   //Update Merchant info from state via ID
   updateMerchant = (id,merchantEditInfo) => {

      const merchants = this.state.merchants

      for (const key in merchants) {
         if (merchants.hasOwnProperty(key)) {
            if (id === merchants[key].id) {

               merchants[key] = merchantEditInfo
               this.setState({ 
                  merchants: merchants,
                  editMerchant: false
               });
            }
         }
      }

      Swal.fire(
         'Updated Merchant Info!',
         'Yey!',
         'success'
      )
   }

   deleteMerchant = (id) => {
   
      Swal.fire({
         title: 'Are you sure?',
         text: "This Merchant will be deleted.",
         type: 'warning',
         showCancelButton: true,
         confirmButtonColor: '#3085d6',
         cancelButtonColor: '#d33',
         confirmButtonText: 'Proceed'
      }).then(async (result) => {
         if (result.value) {
            const merchants = this.state.merchants

            for (const key in merchants) {
               if (merchants.hasOwnProperty(key)) {
                  if (id === merchants[key].id) {

                     merchants.splice(merchants[key],1)
                     this.setState({
                        merchants: merchants,
                        editMerchant: false
                     });
                  }
               }
            }

            await this.setState({
               updateMerchantInfo: this.baseState
            })
         }
      })
   }

   handleLogout = () => {
      Swal.fire({
         title: 'Logout',
         text: "Are you sure you want to logout?",
         type: 'warning',
         showCancelButton: true,
         confirmButtonColor: '#3085d6',
         cancelButtonColor: '#d33',
         confirmButtonText: 'Proceed'
      }).then((result) => {
         if (result.value) {
            auth.logout(() => {
               this.props.history.push('/')
            })
         }
      })



      
   }

   //Reset on cancel edit/add merchant
   reset = () =>{
      this.setState({
         editMerchant: false,
         addMerchant: false,
      })
   }

   //Get input search value 
   getSearch=(e)=> {
      e.preventDefault();
      const { name, value } = e.target
      
      if (/^[a-zA-Z0-9-]{1,10}$/.test(e.target.value)) {
         this.setState({
            merchantSearch: e.target.value
         })
      }

      if(e.target.value === ""){
         this.setState({
            merchantSearch: ""
         })
      }
      
   }

   render() {

      let merchants = this.state.merchants

      //Handle search changes
      let search = this.state.merchantSearch.trim().toLowerCase();
      if (search.length > 0 || search.length === 0) {
         merchants = merchants.filter(function (merchant) {
            return merchant.name.toLocaleLowerCase().match(search)
         })
      }

      let component = null

      //Check which component will be displayed via changes in state
      if (this.state.addMerchant && !this.state.editMerchant){
         component = 
            <AddMerchant 
               enrollMerchant={this.enrollMerchant}
               cancelEdit={this.cancelEdit}
            />
      }

      if (this.state.editMerchant && !this.state.addMerchant) {
         component = 
            <MerchantEdit 
               merchantInfo={this.state.merchantInfo}
               updateMerchant={this.updateMerchant}
               cancelEdit={this.cancelEdit}
            />
      }

      if (!this.state.editMerchant && !this.state.addMerchant) {
         component = 
            <MerchantList 
               deleteMerchant = {this.deleteMerchant} 
               editMerchant ={this.editMerchant} 
               addMerchant={this.addMerchant}
               merchants= {[...merchants]}
            />
      }

      return (
         <div className={classes.MerchantContainer}>

            <div className = {classes.navcontent}>
               <h3><ListIcon /> Merchant Dashboard</h3>
               <SideNav className={classes.SideNav} addMerchant={this.addMerchant} reset={this.reset}/>
            </div>

            <div className={classes.bodycontent}>
               <div className = {classes.bodycontainer}>
                  <div className={classes.topnav}>
                     <div className={classes.burger}>
                        <BurgerMenu 
                           logout = {this.handleLogout}
                           addMerchant = {this.addMerchant}
                           reset= {this.reset}
                        />
                     </div>
                     {
                        !this.state.editMerchant && !this.state.addMerchant ? 
                        <div className={classes.search}>
                           <TextField
                              id="standard-search"
                              label="Search"
                              type="search"
                              name="search"
                              onChange={(data) => this.getSearch(data)}
                              value={this.state.merchantSearch}
                              className={classes.textField}
                              margin="normal"

                              />  
                        </div>
                        : null

                        
                     }
                     
                     <div className={classes.logout}>
                        <p>
                           <Button
                              variant="contained"
                              color="secondary"
                              onClick={this.handleLogout}
                              className={classes.button}>
                              Logout
                           <CloseIcon className={classes.rightIcon} />
                           </Button>
                           
                        </p>
                     </div>

                  </div>
                  <div className={classes.merchantbody}>
                     {component}
                  </div>
               </div>
               
            </div>
         </div>
      );

   }
}

export default MerchantContainer;
