import React from 'react';
// import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add'

import Merchant from '../Merchant/Merchant'
import classes from '../MerchantList/MerchantList.module.css'

class MerchantList extends React.Component{

   state = {

   }
   
   render() {
      const merchants = 
         this.props.merchants.map((merchant,i) => (
         <Merchant 
            name={merchant.name}
            deleteMerchant={()=>this.props.deleteMerchant(merchant.id)}
            editMerchant={()=>this.props.editMerchant(merchant.id)}
            key={i} />
      ))

      return(
         <div>
            <div className={classes.merchantListHeader}>
               <div className={classes.headerText}>
                  <p>Merchant List</p> 
               </div>
               <div className={classes.headerButton}>
                  <p>
                     <Button onClick={this.props.addMerchant} variant="contained" color="default" className={classes.button}>
                        Add
                        <AddIcon className={classes.rightIcon} />
                     </Button>
                  </p>
               </div>
            </div>

            <div className={classes.merchantList}>
               {merchants}

            </div>
         </div>
         
      )
   }
}

export default MerchantList